$fontRoboto: Roboto;
$primary-color: #060b35;
$second-color: #616070;
$colorwhite: #fff;
$colorblueacent: #00acdc;

body {
  display: flex;
  justify-content: center;
  margin: 0;
  background-color: rgb(180, 178, 175);
}

.card {
  border-radius: 7px;
  position: relative;
  margin-top: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  width: 200px;
  height: 408px;

  &__content {
    padding: 20px;
  }

  &__image {
    margin-top: 33px;
    width: 160px;
    height: 134px;
  }

  &__description {
    color: $primary-color;
    font-size: 12px;
    font-family: $fontRoboto;
    font-weight: 500;
    line-height: 18px;
    margin-top: 8px;
  }

  &__productcode {
    margin-top: 10px;
    color: $second-color;
    font-family: $fontRoboto;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &__productcode {
    margin-top: 10px;
    color: $second-color;
    font-family: $fontRoboto;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &__starsreviews {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
  }

  &__reviews {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &__wordprice {
    margin-top: 17px;
    display: flex;
    justify-content: space-between;
  }

  &__word {
    color: $second-color;
    font-weight: 400;
    size: 12px;
    line-height: 18px;
  }

  &__price {
    color: $primary-color;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
}

.button--large {
  background-color: $colorblueacent;
  text-transform: uppercase;
  width: 166px;
  height: 40px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: $colorwhite;
  border-radius: 5px;
}

.button--hover:hover {
  color: $colorblueacent;
  background-color: $colorwhite;
  border: 1px solid $colorblueacent;
}
